import cTitle from 'components/title';
import { Navbar } from 'mint-ui';
export default {
    data() {
        return {
            datas: [],
            typeData: [],
            popupSpecs: false,
        };
    },


    activated() {
        this.datas = [];
        //获取数据
        this.getData("");
    },
    methods: {
        //获取数据
        getData(_type) {
            let that = this;
            let json = { type: _type };
            $http.get('finance.income.get-income-list', json, "加载中...").then(function (response) {
                console.log(response);
                if (response.result == 1) {
                    that.datas = response.data;
                } else {
                    console.log(response.msg);
                }

            }, function (response) {
                console.log(response);
            });
        },

        //获取类型数据
        getTypeData() {
            let that = this;
            let json = {};
            $http.get('finance.income.get-search-type', json, "获取中...").then(function (response) {
                console.log(response);
                if (response.result == 1) {
                    that.typeData = response.data;
                } else {
                    console.log(response.msg);
                }

            }, function (response) {
                console.log(response);
            });
        },

        //返回
        goto() {
            window.history.go(-1);
        },

        //筛选
        screen() {
            if (this.typeData.length == 0) {
                this.getTypeData();
            }
            this.popupSpecs = true;

        },

        //点击筛选处理
        screenType(_type) {


            this.popupSpecs = false;
            console.log(_type);
            this.datas = [];
            this.getData(_type);
        }

    },


    components: { cTitle }
}


